.contact-form{
padding: 0 3rem 0 3rem;
display: flex;
height: 80vh;
margin-top: 10rem;
margin-bottom: 5rem;

}
.c-right{
display: flex;
justify-content: center;
position: relative;
flex: 1;
}
.c-right>form{
display: flex;
flex-direction: column;
gap: 2rem;
align-items: center;
}
.c-right  .user{
    width: 20rem;
    height: 2rem;
    padding: 0.3rem;
    outline: none;
    border: 2px solid var(--orange);
    border-radius: 20px;
}
textarea{
    height: 4rem!important;
}
.c-blur1{
top:1rem;
left: 8rem;
}
@media (max-width: 550px) {
    .contact-form{
     flex-direction: column;   
     margin-top: 3rem;
     height: fit-content;     
    }
}
@media (max-width: 446px) {
    .c-right{
     transform: scale(.8);  
    }
    .awesome{
        display: flex;
    }
    .d-button{
        transform: scale(1.2);
    }
}

